@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
@import url(https://use.typekit.net/cxp5wyv.css);
.alert {
  padding: 0.8rem;
  margin: 1rem 0;
  opacity: 0.9;
  background: var(--light-color);
  color: green;
}

.btn-danger,
.bg-danger,
.badge-danger,
.alert-danger {
  background: var(--danger-color);
  color: red;
}

.auth-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
}

.auth-inner {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  width: 445px;
  height: 553px;
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all 0.3s;
}

.auth-wrapper .form-control:focus {
  border-color: #167bff;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}

.forgot-password a {
  color: #0091ff;
}

.inputGroup input {
  border: none;
}

.inputGroup .input-group-text {
  border: none;
  background-color: #ffffff;
}

.inputGroup svg {
  color: #adb5bd;
}

.primary-bg {
  background-color: #0091ff !important;
}

.font-14 {
  font-size: 14px !important;
}

.mt-5 {
  margin-top: 5rem;
}
.gradient-info {
  height: 500px;
  position: relative;
  background: #0091ff 0% 0% no-repeat padding-box;
}

.separator {
  position: absolute;
  top: auto;
  left: 0;
  right: 0;
  width: 100%;
  height: 150px;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  overflow: hidden;
  pointer-events: none;
  top: auto;
  bottom: 0;
}

.separator-skew {
  height: 60px;
}

.separator svg {
  position: absolute;
  pointer-events: none;
  bottom: 0;
}

.separator .fill-default {
  fill: white;
}

@media (min-width: 1200px) {
  .separator-skew {
    height: 80px;
  }
}

